import React, { useContext, useEffect, useState } from 'react';
import Header from "./Header";
import Loader from "../../../component/Loader";
import ConnectToWalletModal from "../../ConnectToWalletModal";
import { AppContextType } from "../../../context/context_types";

import style from './Layout.module.scss';

export default function Layout(props) {
  const { connectToWalletModalVisible, setConnectToWalletModalVisible } = useContext(AppContextType);
  const [loaded, setLoaded] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  useEffect(() => {
    setTimeout(() => setLoaded(true), 2000);
  }, []);

  return (
    <div className={style.container}>
      <div className={style.content}>
        <Header
          menuOpen={isOpen}
          onHamburgerClick={() => setIsOpen(!isOpen)}
          wrapperClass={style.header}
        />
        <main className={style.main}>
          {props.children}
        </main>
      </div>
      <Loader isVisible={!loaded} />
      <ConnectToWalletModal
        visible={connectToWalletModalVisible}
        onCancel={() => setConnectToWalletModalVisible(false)}
      />
    </div>
  )
}
